:root {
  --blue: #2196F3;
  --blue-dark: #1D87DA;
  --indigo: #303F9F;
  --purple: #673AB7;
  --pink: #FF4081;
  --red: #F6685E;
  --red-dark: #E5625D;
  --orange: #FF9800;
  --yellow: #FFEB3B;
  --green: #4CAF50;
  --green-dark: #449D48;
  --cyan: #0097A7;
  --white: #FFFFFF;
  --gray: #9E9E9E;
  --gray-light: #BDBDBD;
  --gray-dark: #616161;
  --light: #F5F5F5;
  --dark: #212121;
  --success: #4CAF50;
  --info: #0097A7;
  --warning: #FFEB3B;
  --danger: #F44336;
}

* {
  box-sizing: border-box;
  -webkit-appearance: none;
}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

p {
  margin: 0;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: 600;
}

#root {
  height: 100%;
}